@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@300&display=swap);

.navbar {
  background: var(--primaryColor);
  color: white;
  padding: 10px;
}

.navbar__home {
  font-family: "Permanent Marker", cursive;
  font-size: 1.4em;
}

/* .navbar__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
} */

.navbar__link {
  display: none;
  font-family: "Lato", sans-serif;
  font-size: 1.2em;
  cursor: pointer;
  margin: 8px;
}

/* .navbar__link:hover {
  background: var(--secondaryColor);
} */

.navbar__link--show {
  display: block;
  margin: 8px;
}

.navbar__toggler {
  border: none;
  /* border-color: initial;
  border-style: none;
  border-width: initial; */
  color: white;
  background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
  background: initial;
  font-size: 1.3em;
  position: absolute;
  padding: 10px;
  right: 0;
  top: 0;
}

.navbar__toggler:hover {
  background: var(--secondaryColor);
}

@media (min-width: 768px) {
  .navbar__link {
    display: inline;
  }
  .navbar__toggler {
    display: none;
  }
}

.hero {
  align-items: center;
  background: url("https://user-images.githubusercontent.com/38108288/98447858-84feb880-20fe-11eb-8067-2825f5d4a2a2.jpg");
  color: #ffff;
  display: flex;
  justify-content: center;
  text-align: center;
  height: 100vh;
  background-position: 40% 90%;
  background-repeat: no-repeat;
  background-size: cover;
}

.hero__title {
  font-family: "Permanent Marker", cursive;
  font-size: 2.5em;
  margin: 10px;
}

.hero__degree {
  font-family: "Lato", sans-serif;
  font-size: 2em;
  margin: 10px;
}

.hero__list {
  list-style: none;
}

.hero__item {
  display: inline;
  font-size: 2.25rem;
  padding: 1em;
}

.hero__link {
  color: white;
  transition: 0.5s ease all;
}

.hero__link:hover {
  color: var(--primaryColor);
  font-size: 46px;
}

@media only screen and (min-width: 768px) {
  .hero {
    background-attachment: scroll;
    background-attachment: fixed;
    background-position: 40% 25%;
  }
}

.container {
  margin: auto;
  width: 90%;
}

.container__title {
  color: white;
  font-family: "Lato", sans-serif;
}

.hide-children {
  display: none;
}

.show-children {
  display: block;
}

.container__show-more {
  text-align: center;
}

.container__button {
  color: white;
  font-size: 26px;
  cursor: pointer;
  background: none;
  border: none;
}

.about {
  color: white;
}

.about__text {
  font-family: "Source Code Pro", monospace;
  line-height: 1.5em;
}

.workExperience__title {
  -webkit-text-decoration: underline #3a63ff;
          text-decoration: underline #3a63ff;
}

.work-experience-card {
  background: white;
  border: 1px solid #e6e6e6;
  height: auto;
  margin: 10px 0;
  padding: 15px;
  word-wrap: break-word;
}

.work-experience-card__company {
  font-size: 1.5em;
  font-weight: 300;
  color: #374054;
  display: inline-block;
  margin: 0;
}

.work-experience-card__employment {
  font-size: 1.2em;
  font-weight: 300;
  color: #7e8890;
  margin: 0 0 15px 0;
}

.work-experience-card__description {
  color: #74808a;
  font-size: 1em;
  margin: 0;
}

.work-experience-card__date {
  float: right;
}

.education__title {
  -webkit-text-decoration: underline #3a63ff;
          text-decoration: underline #3a63ff;
}

.education {
  max-width: 700px;
  margin: 0 auto 30px auto;
  padding: 15px;
  border: 1px solid #dcd9d9;
  text-align: left;
  background: white;
}

.education__institution {
  font-weight: 500;
  /* font-family: importar Lato de google fonts*/
  margin: 0;
  color: #374054;
}

.education__date {
  color: #74808a;
  float: right;
}

.education__degree {
  color: #74808a;
  clear: both;
  font-weight: 500;
  margin: 0 0 15px 0;
}

.project {
  display: flex;
  max-width: 700px;
  margin: 0 auto 30px auto;
  padding: 15px;
  border: 1px solid #dcd9d9;
  text-align: left;
  background: white;
}

.project_img {
  width: 300px;
  height: 300px;

  display: none;
}

.project__info {
  align-self: center;
  text-align: center;
  justify-self: center;
  width: 100%;
}

.project__title {
  font-weight: 500;
  margin: 0 0 15px 0;
  color: #374054;
}

.project__description {
  color: #74808a;
  font-size: 18px;
  font-weight: 500;
  margin: 0 0 15px 0;
}

.proyect__link {
  color: var(--primaryColor);
  text-decoration: none;
}

.proyect__link:hover {
  text-decoration: underline;
}

@media (min-width: 768px) {
  .project_img {
    display: inline;
    display: initial;
  }
}

.abilities__title {
  -webkit-text-decoration: underline #3a63ff;
          text-decoration: underline #3a63ff;
}

.abilities {
  margin-bottom: 10px;
}

.abilities__list {
  display: block;
  margin: 0 auto;
  padding: 0;
  max-width: 800px;
}

.abilities__item {
  display: inline-block;
  margin: 7px;
  padding: 5px 10px;
  color: #374054;
  background: #e4e4ea;
  list-style: none;
  cursor: default;
  font-size: 1.2em;
}

.diploma {
  max-width: 700px;
  margin: 0 auto 30px auto;
  padding: 15px;
  border: 1px solid #dcd9d9;
  text-align: left;
  background: white;
}

.diploma__title {
  font-weight: 500;
  /* font-family: importar Lato de google fonts*/
  margin: 0 0 10px 0;
  color: #374054;
}

.diploma__date {
  color: #74808a;
  float: right;
}

.diploma__link {
  color: #74808a;
  clear: both;
  font-weight: 500;
  margin: 10px 0 10px 0;
  text-decoration: none;
}

.contact {
  margin: 10px;
}

.contact__email {
  width: 100%;
  margin: 5px 0;
  padding: 10px;
  border-radius: 4px;
  border: none;
  box-sizing: border-box;
}

.contact__text-area {
  width: 100%;
  margin: 5px 0;
  padding: 10px;
  border-radius: 4px;
  border: none;
  box-sizing: border-box;
}

.contact__button {
  width: 100%;
  text-transform: capitalize;
  background: #fff;
  border-radius: 4px;
  padding: 5px 10px;
  border: none;
  color: #3498db;
  font-weight: 700;
  transition: 0.5s ease all;
}

.contact__button:hover {
  background-color: var(--primaryColor);
  color: white;
  cursor: pointer;
}

.contact__message {
  text-align: center;
  font-size: 2.5em;
  color: white;
}

.footer {
  background: white;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
}

.footer__owner {
  align-items: center;
  display: flex;
  margin: 0;
  justify-content: center;
  font-size: 1.1em;
}

.footer__up-arrow {
  align-items: center;
  display: flex;
  margin-bottom: 17.6px;
  justify-content: center;
  font-size: 36px;
}
.footer__up-arrow:hover {
  color: var(--primaryColor);
  cursor: pointer;
}

.footer__list {
  list-style: none;
  /* display: none; */
}

.footer__item {
  font-size: 26px;
  display: inline;
  padding: 1em;
}

.footer__link {
  color: black;
}

.footer__socials {
  display: flex;
  justify-content: space-evenly;
}

.footer__social {
  font-size: 26px;
}

@media only screen and (min-width: 768px) {
  .footer {
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    padding: 25px;
    margin-top: 30px;
  }
  .footer__up-arrow {
    margin: 0;
  }
  /* .footer__list {
    display: inherit;
  } */
}

:root {
  --primaryColor: #3a63ff;
  --secondaryColor: #343334;
}

body {
  background: #343334;
}

.contenedor {
  height: 1000px;
}
